import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import Medical from "../../../../assets/images/medical.jpg";

const MedicalClaims = () => {
  const [claims, setClaims] = useState([]);

  useEffect(() => {
    getClaims();
  }, []);

  const getClaims = async () => {
    let userId = JSON.parse(localStorage.getItem("user_id"));
    try {
      let url = `http://142.93.207.225:4334/getAllUserMedicalAidClaimsByUserId/${userId}`;
      let { data } = await axios.get(url);
      console.log("response[0]", data);
      setClaims(data);
    } catch (ex) {
      console.error(`:: allPolicies  :: ${ex.message}`);
    }
  };

  return (
    <div>
      {claims.length == 0 ? (
              <div className="p-t-50 p-l-80 ">
                  <p className="l-text1"> No medical claims</p>
        </div>
      ) : (
        <div>
          {claims.map((claim, i) => (
            <div className=" p-b-20">
              <div className="row no-gutters">
                <div className="col-md-2 ">
                  <div className="w-size3 shadow1">
                    <img src={Medical} className="w-size6 bo-rad-5" alt="..." />
                  </div>
                </div>
                <div className="col-md-10 bg5">
                  <div className="d-flex h-size5">
                    <div className="col-md-8">
                      <p className="block2-name dis-block s-text4 p-b-5">
                        Claim
                      </p>
                      <p className="s-text1">content here</p>
                      <p className="block2-price s-text4 p-r-5">some here</p>
                    </div>
                    <div className="col-md-4 ">
                      <div className="p-t-70">
                        <Link
                          to={`/`}
                          class="flex-c-m size2 s-text2 bo3 bo-rad-5 hov1 trans-0-4"
                        >
                          More Details
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};
export default MedicalClaims;
