import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import swal from "sweetalert";
import "./Policy.css";

import Car from "../../../assets/images/motor1.jpg";

import Banner from "../../../assets/images/banner.jpg";

// insurance images
import Motor from "../../../assets/images/motor.jpg";
import Funeral from "../../../assets/images/funeral.jpg";
import Medical from "../../../assets/images/medical.jpg";

// Components

import Vehicles from "./components/Vehicles";
import DependentsFuneral from "./components/DependentsFuneral";
import DependentsMedical from "./components/DependentsMedical";

const apiUrl = "http://142.93.207.225:4333";

const Policy = ({ match }) => {
  const [item, setItem] = useState({});
  const [policy, setPolicy] = useState([]);
  const [company, setCompany] = useState({});

  const history = useHistory();

  useEffect(() => {
    fetchItem();
  }, []);

  const fetchItem = async () => {
    try {
      const fetchItem = await axios.get(
        `http://142.93.207.225:4333/getUserPolicyByPolicyNo/${match.params.policy_no}/${match.params.id}`
      );
      console.log("policy.js response", fetchItem.data[0]);
      setItem(fetchItem.data[0]);

      const fetchCompany = await axios.get(
        `http://142.93.207.225:4333/getCompanyByCompanyId/${fetchItem.data[0].company_id}`
      );
      console.log("getCompany response", fetchCompany.data[0]);
      setCompany(fetchCompany.data[0]);
    } catch (ex) {
      console.error(`:: policy  :: ${ex.message}`);
    }
  };

  const handleClaim = () => {
    swal({
      title: "Motor insurance",
      text: "payment successful!",
      icon: "success",
      button: "Ok",
    });
    history.push("/");
  };

  const handleUnRegister = () => {
    let user = [
      {
        person_id: match.params.id,
        policy_id: match.params.policy_no,
      },
    ];
    swal({
      title: "Are you sure?",
      text: "Once unregistered, you will not be able to view policy",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        axios
          .post("http://142.93.207.225:4333" + "/unregisterUserPolicy", user)
          .then(async (response) => {
            console.log(response);
            swal(`${company.name} has been unregistered`, {
              icon: "success",
            });
          })
          .catch((error) => {
            console.log("::log in error::", error);
          });
      } else {
        swal(`You are still registered to ${company.name}`);
      }
    });
  };

  return (
    <div className="">
      <div className="bread-crumb flex-w p-l-20 p-t-10 p-b-20 p-l-15-sm">
        <Link to="/" className="s-text16">
          Home
          <i className="fa fa-angle-right m-l-8 m-r-9" aria-hidden="true"></i>
        </Link>
        <Link to="/motor" className="s-text16">
          Policies
          <i className="fa fa-angle-right m-l-8 m-r-9" aria-hidden="true"></i>
        </Link>
        <span className="s-text17">{company.name}</span>
      </div>
      <div class="banner pos-relative mx-auto bo-rad-5 p-t-20">
        <div className="pos3 d-flex p-l-20">
          {(() => {
            switch (item.type) {
              case "1":
                return (
                  <img
                    class=" img-thumbnail"
                    src={Motor}
                    width="150"
                    height="150"
                    style={{ marginBottom: -30 }}
                  />
                );
              case "2":
                return (
                  <img
                    class=" img-thumbnail"
                    src={Funeral}
                    width="150"
                    height="150"
                    style={{ marginBottom: -30 }}
                  />
                );
              case "3":
                return (
                  <img
                    class=" img-thumbnail"
                    src={Medical}
                    width="150"
                    height="150"
                    style={{ marginBottom: -30 }}
                  />
                );
            }
          })()}
          <p className="m-text1 p-t-40 p-l-20"> {company.name}</p>
        </div>
        <div className="pos4 m-r-20 d-flex">
          <p className="s-text1"> {company.email}</p> &nbsp; &nbsp;
          <p className="s-text1"> {company.tel}</p>
        </div>
      </div>

      <div className=" p-t-40">
        <div className="row ">
          <div className="col-md-5 m-l-20">
            <div className="p-t-30 respon5">
              <h4 className="product-detail-name s-text2 p-b-13">
                Plan: <span> </span>
              </h4>

              <h4 className="product-detail-name s-text2 p-b-13">
                Policy No: <span />
                {item.policy_no}
              </h4>
              <h4 className="product-detail-name s-text2 p-b-13">
                Date Registered: <span />
              </h4>
              <h4 className="product-detail-name s-text2 p-b-13">
                Processing time: <span /> <span />
              </h4>

              <div className="p-t-33 p-b-60 ">
                <div className="flex-m flex-w">
                  <div className="purchase-product size1 trans-0-4 m-t-10 m-b-10">
                    <button
                      className="flex-c-m size2 s-text2 bo3 bg1 hov4 bo-rad-5 trans-0-4"
                      type="button"
                      onClick={handleUnRegister}
                    >
                      Unregister Policy
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-6">
            {(() => {
              switch (item.type) {
                case "1":
                  return (
                    <Vehicles
                      userId={item.person_id}
                      policyId={item.policy_no}
                    />
                  );
                case "2":
                  return (
                    <DependentsFuneral
                      userId={item.person_id}
                      policyId={item.policy_no}
                    />
                  );
                case "3":
                  return (
                    <DependentsMedical
                      userId={item.person_id}
                      policyId={item.policy_no}
                    />
                  );
              }
            })()}
          </div>
        </div>
      </div>
    </div>
  );
};
export default Policy;
