import React, { Component } from "react";
import { Link } from "react-router-dom";
import Medical from "../../../assets/images/medical.jpg";
import axios from "axios";

class Medicals extends Component {
  constructor(props) {
    super(props);

    this.state = {
      medicals: [],
      medicalqoutes: {
        id: "",
        company_id: "",
        category: "",
        subcategory: "",
        price: "",
        period: "",
        processing_time: "",
        name: "",
        value: "",
        car_quotation_id: "",
        status: "",
      },
    };

    this.medicalQuotes = this.medicalQuotes.bind(this);
  }

  componentDidMount() {
    (async () => {
      await this.medicalQuotes();
    })();
  }

  medicalQuotes = async () => {
    try {
      let url = `http://142.93.207.225:4335/getAllMedicalQuotes`;
      let { data } = await axios.get(url);
      this.setState({ medicals: data[0] });
      console.log("medical qoutations ::", this.state.medicals);
      console.log("specific qoute", this.state.medicals[0][0]);
    } catch (ex) {
      console.error(`:: medicalQuotes  :: ${ex.message}`);
    }
  };

  render() {
    return (
      <React.Fragment>
        <section className=" p-t-10 col-md-12">
          <div className="bread-crumb flex-w p-l-20 p-t-10 p-l-15-sm">
            <Link to="/" className="s-text16">
              Home
              <i
                className="fa fa-angle-right m-l-8 m-r-9"
                aria-hidden="true"
              ></i>
            </Link>
            <span className="s-text17">Medical</span>
          </div>

          <div className="p-l-20 p-t-10 p-b-50 col-md-12 h-size2">
            {this.state.medicals.map((medical, i) => (
              <div className=" p-b-20" key={medical[0][0].id}>
                <div className="row no-gutters">
                  <div className="col-md-2 ">
                    <div className="w-size3 shadow1">
                      <img src={Medical} className="w-size6 bo-rad-5" alt="..." />
                    </div>
                  </div>
                  <div className="col-md-10 bg5">
                    <div className="d-flex h-size5">
                      <div className="col-md-8">
                        <p className="block2-name dis-block s-text4 p-b-5">
                          Medical insurance
                        </p>
                        <p className="s-text1">
                          Health insurance is a type of insurance coverage that
                          pays for medical and surgical expenses that are
                          incurred by the insured.
                        </p>
                        <p className="block2-price s-text4 p-r-5">
                          $ {medical[1][0].amount}
                        </p>
                      </div>
                      <div className="col-md-4 ">
                        <div className="p-t-70">
                          <Link
                            to={`/medical/${medical[0][0].id}`}
                            class="flex-c-m size2 s-text2 bo3 bo-rad-5 hov1 trans-0-4"
                          >
                            Am interested
                          </Link>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </section>
      </React.Fragment>
    );
  }
}
export default Medicals;
