import React, { useEffect, useState } from "react";
import axios from "axios";
import { Modal } from "react-bootstrap";
import Home from "@material-ui/icons/Home";
import { Link, useHistory } from "react-router-dom";
import swal from "sweetalert";

const DependentsMedical = (props) => {
  const [beneficiaries, setBeneficiaries] = useState([]);
  const [dependent, setDependent] = useState({
    name: "",
    surname: "",
    national_id: "",
    address: "",
    email: "",
    user_id: props.userId,
    policy_id: props.policyId,
    policy_no: "",
    dob: "",
    relation_to_member: "",
    occupation: "",
    prefix: "",
    cell: "",
  });
  const [claim, setClaim] = useState({
    user_id: props.userId,
    policy_id: props.policyId,
    doctor: "",
    conditions: "",
    patient_type: "",
    anaesthetist: "",
    beneficiary_id: "",
    surgical_assistant: "",
    amount_claimed: "",
    refering_practitioner: "",
  });

  const [showAdd, setShowAdd] = useState(false);
  const [showEdit, setShowEdit] = useState(false);
  const [showClaim, setShowClaim] = useState(false);

  const handleEditClose = () => setShowEdit(false);
  const handleEditShow = () => setShowEdit(true);

  const handleClaimClose = () => setShowClaim(false);

  // **************** ADD DEPENDENT ******************************************************
  const handleAddClose = () => setShowAdd(false);
  const handleAddShow = () => setShowAdd(true);

  const handleDependentSubmit = () => {
    const regDependent = [dependent];
    console.log("::  REG INFO ::", regDependent);
    axios
      .post(
        "http://142.93.207.225:4333" + "/registerNewMedicalBeneficiary",
        regDependent
      )
      .then(async (response) => {
        console.log(response.data);
        setShowAdd(false);
        swal({
          title: "Success",
          text: `${dependent.name} has been added to your account`,
          icon: "success",
          button: "Ok",
        });
      })
      .catch((error) => {
        console.log("::log in error::", error);
      });
  };

  // **************** MAKE CLAIM ******************************************************
  const handleClaimShow = () => {
    setShowClaim(true);
  };

  const handleClaim = (value) => {
    handleClaimShow();
    setClaim({ ...claim, beneficiary_id: value });
  };
  const handleClaimSubmit = () => {
    let details = [claim];
    console.log("::claim details", details);
    setShowClaim(false);
    axios
      .post("http://142.93.207.225:4334" + "/makeMedicalAidClaim", details)
      .then(async (response) => {
        console.log(response.data);
        swal({
          title: "Success",
          text: "Your claim has been submitted!",
          icon: "success",
          button: "Ok",
        });
      })
      .catch((error) => {
        console.log("::log in error::", error);
      });
  };

  useEffect(() => {
    getMedBenefiaries();
    console.log(":: Vehicle props::", props);
  }, []);

  const getMedBenefiaries = async () => {
    try {
      let url = `http://142.93.207.225:4333/getMedicalBeneficiariesByUserId/${props.userId}`;
      let { data } = await axios.get(url);
      console.log(":: User Beneficiaries ::", data);
      setBeneficiaries(data);
    } catch (ex) {
      console.error(`:: Beneficiaries error  :: ${ex.message}`);
    }
  };

  return (
    <div className="w-size6">
      <div className="">
        <div className="purchase-product size3 trans-0-4 m-t-10 m-b-10">
          <button
            className="flex-c-m size2 s-text2 bo3 hov4 bo-rad-5 trans-0-4"
            type="button"
            onClick={handleAddShow}
          >
            Add
          </button>
        </div>
      </div>
      <table className="table table-borderless">
        <thead>
          <tr className="s-text2">
            <th>Name</th>
            <th>Surname</th>
            <th>ID</th>
            <th>relationship</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody className="s-text1">
          {beneficiaries.map((beneficiary, i) => (
            <tr key={beneficiary.id} data-id={beneficiary.id}>
              <td>{beneficiary.name}</td>
              <td>{beneficiary.surname}</td>
              <td>{beneficiary.national_id}</td>
              <td>{beneficiary.relation_to_member}</td>
              <td>
                <p className="p-l-10" title="Edit" onClick={handleEditShow}>
                  Edit
                </p>
                <p
                  className="p-l-10"
                  title="Claim"
                  onClick={() => handleClaim(beneficiary.id)}
                >
                  Claim
                </p>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* ***************************** REGISTER DEPENDENT ************************ */}
      <Modal show={showAdd} onHide={handleAddClose} centered>
        <Modal.Header closeButton>
          <p className="s-text2">Add beneficiary</p>
        </Modal.Header>
        <Modal.Body>
          <form>
            <p className="s-text2 " style={{ marginBottom: 5 }}>
              Name
            </p>
            <div class="size15 bo4">
              <input
                value={dependent.name}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="name"
                placeholder="Name"
                onChange={(e) =>
                  setDependent({ ...dependent, name: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Surname
            </p>
            <div class="size15 bo4">
              <input
                value={dependent.surname}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="surname"
                placeholder="Surname"
                onChange={(e) =>
                  setDependent({ ...dependent, surname: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              National ID
            </p>
            <div class="size15 bo4">
              <input
                value={dependent.national_id}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="national_id"
                placeholder="National id"
                onChange={(e) =>
                  setDependent({ ...dependent, national_id: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Date of birth
            </p>
            <div class="size15 bo4">
              <input
                value={dependent.dob}
                class="sizefull s-text1 p-l-20"
                type="date"
                id="dob"
                placeholder="Date of birth"
                onChange={(e) =>
                  setDependent({ ...dependent, dob: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Relationship to member
            </p>
            <div class="size15 bo4">
              <input
                value={dependent.relation_to_member}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="relation_to_member"
                placeholder="Relation to member"
                onChange={(e) =>
                  setDependent({
                    ...dependent,
                    relation_to_member: e.target.value,
                  })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Occupation
            </p>
            <div class="size15 bo4">
              <input
                value={dependent.occupation}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="occupation"
                placeholder="Occupation"
                onChange={(e) =>
                  setDependent({ ...dependent, occupation: e.target.value })
                }
              />
            </div>
          </form>
        </Modal.Body>
        <Modal.Footer>
          <div className="d-flex">
            <button
              className="flex-c-m size3 s-text2 bo3 hov1 bo-rad-5 trans-0-4"
              type="button"
              onClick={handleAddClose}
            >
              Close
            </button>
            <button
              className="flex-c-m m-l-20 size3 s-text2 bo3 hov1 bo-rad-5 bg1 trans-0-4"
              type="button"
              onClick={handleDependentSubmit}
            >
              Add dependency
            </button>
          </div>
        </Modal.Footer>
      </Modal>

      <Modal show={showEdit} onHide={handleEditClose} centered>
        <Modal.Header closeButton>
          <p className="s-text2">Edit details</p>
        </Modal.Header>
        <Modal.Body>
          <p className="s-text2 " style={{ marginBottom: 5 }}>
            Name
          </p>
          <div class="size15 bo4">
            <input
              class="sizefull s-text1 p-l-20"
              type="text"
              name="make"
              placeholder=" Make"
            />
          </div>
          <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
            Registration No
          </p>
          <div class="size15 bo4">
            <input
              class="sizefull s-text1 p-l-20"
              type="text"
              name="registration_no"
              placeholder="Registration number"
            />
          </div>
          <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
            Model Year
          </p>
          <div class="size15 bo4">
            <input
              class="sizefull s-text1 p-l-20"
              type="text"
              name="model_year"
              placeholder="model year"
            />
          </div>
          <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
            Engine No
          </p>
          <div class="size15 bo4">
            <input
              class="sizefull s-text1 p-l-20"
              type="text"
              name="engine_no"
              placeholder="engine number"
            />
          </div>
        </Modal.Body>
        <Modal.Footer>
          <div className="d-flex">
            <button
              className="flex-c-m size3 s-text2 bo3 hov1 bo-rad-5 trans-0-4"
              type="button"
              onClick={handleEditClose}
            >
              Close
            </button>
            <button
              className="flex-c-m m-l-20 size3 s-text2 bo3 hov1 bo-rad-5 bg1 trans-0-4"
              type="button"
              onClick={handleEditClose}
            >
              Save Changes
            </button>
          </div>
        </Modal.Footer>
      </Modal>

      {/* *************************CLAIM FOR DEPENDENT ******************************* */}

      <Modal show={showClaim} onHide={handleClaimClose} centered>
        <Modal.Header closeButton>
          <p className="s-text2">Make Claim</p>
        </Modal.Header>
        <Modal.Body>
          <form>
            <p className="s-text2 " style={{ marginBottom: 5 }}>
              Doctor
            </p>
            <div class="size15 bo4">
              <input
                value={claim.doctor}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="doctor"
                placeholder="Enter Doctor's name"
                onChange={(e) => setClaim({ ...claim, doctor: e.target.value })}
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Medical Condition
            </p>
            <div class="size15 bo4">
              <input
                value={claim.conditions}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="conditions"
                placeholder="Condition"
                onChange={(e) =>
                  setClaim({ ...claim, conditions: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Patient Type
            </p>
            <div class="size15 bo4">
              <input
                value={claim.patient_type}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="patient_type"
                placeholder="Patient type"
                onChange={(e) =>
                  setClaim({ ...claim, patient_type: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Anaesthetist
            </p>
            <div class="size15 bo4">
              <input
                value={claim.anaesthetist}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="anaesthetist"
                placeholder="Attending anaesthetist"
                onChange={(e) =>
                  setClaim({ ...claim, anaesthetist: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Assistant Doctor
            </p>
            <div class="size15 bo4">
              <input
                value={claim.surgical_assistant}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="surgical_assistant"
                placeholder="Assistant doctor"
                onChange={(e) =>
                  setClaim({ ...claim, surgical_assistant: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Amount to be claimed
            </p>
            <div class="size15 bo4">
              <input
                value={claim.amount_claimed}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="amount_claimed"
                placeholder="Enter amount"
                onChange={(e) =>
                  setClaim({ ...claim, amount_claimed: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Refering Practitioner
            </p>
            <div class="size15 bo4">
              <input
                value={claim.refering_practitioner}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="refering_practitioner"
                placeholder="Refering practitioner"
                onChange={(e) =>
                  setClaim({ ...claim, refering_practitioner: e.target.value })
                }
              />
            </div>
          </form>
        </Modal.Body>
        <Modal.Footer>
          <div className="d-flex">
            <button
              className="flex-c-m size3 s-text2 bo3 hov1 bo-rad-5 trans-0-4"
              type="button"
              onClick={handleClaimClose}
            >
              Close
            </button>
            <button
              className="flex-c-m m-l-20 size3 s-text2 bo3 hov1 bo-rad-5 bg1 trans-0-4"
              type="button"
              onClick={handleClaimSubmit}
            >
              Send Claim
            </button>
          </div>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default DependentsMedical;
