import React, { Component } from "react";
import Skeleton from "react-loading-skeleton";
import { Link } from "react-router-dom";
import axios from "axios";
import ITEM07 from "../../../assets/images/item-07.jpg";
import motor from "../../../assets/images/motor.jpg";
import medical from "../../../assets/images/medical.jpg";
import funeral from "../../../assets/images/funeral.jpg";

class Claims extends Component {
    constructor(props) {
        super(props);

        this.state = {
            policies: [],
            info: {
                id: "",
                company: "",
                address: "",
                email: "",
                cell: "",
                policy_no: "",
                type: "",
                status: "",
            },
            newPolicies: [],
            loading: true,
        };

        this.allPolicies = this.allPolicies.bind(this);
    }

    componentDidMount() {
        (async () => {
            await this.allPolicies();
        })();
    }

    allPolicies = async () => {
        try {
            let url = `http://142.93.207.225:4333/getUserPoliciesByUserId/${JSON.parse(
                localStorage.getItem("user_id")
            )}`;
            let { data } = await axios.get(url);
            console.log("response[0]", data);
            console.log("::DATA INDEX::", data.length);
            this.setState({ policies: data });

            let newArray = [];

            for (let i = 0; i < data.length; i++) {
                console.log(":: INDEX VALUE ::", i);
                console.log(":: COMPANY ID ::", data[i].company_id);
                let company_data = await axios.get(
                    `http://142.93.207.225:4333/getCompanyByCompanyId/${data[i].company_id}`
                );
                console.log("::companyByCompanyId::", company_data.data);
                this.setState((prevState) => ({
                    info: {
                        ...prevState.info,
                        company: company_data.data[0].name,
                        id: company_data.data[0].id,
                        address: company_data.data[0].address,
                        email: company_data.data[0].email,
                        cell: company_data.data[0].cell,
                        policy_no: this.state.policies[i].policy_no,
                        type: this.state.policies[i].type,
                        status: company_data.data[0].status,
                    },
                }));

                this.setState({
                    newPolicies: [...this.state.newPolicies, this.state.info],
                });
                this.setState({
                    loading: false,
                });

                console.log(":: INFO STATE ::", this.state.info);
            }

            console.log(":: FINAL NEW POLICIES ARRAY ::", this.state.newPolicies);
        } catch (ex) {
            console.error(`:: allPolicies  :: ${ex.message}`);
        }
    };

  render() {
    const SkeletonComponent = () => (
      <section className=" p-t-10 col-md-12">
        <div className="bread-crumb flex-w p-l-20 p-t-10 p-l-15-sm">
          <Link to="/" className="s-text16">
            Home
            <i className="fa fa-angle-right m-l-8 m-r-9" aria-hidden="true"></i>
          </Link>
          <span className="s-text17">Claims</span>
        </div>

        <div className="p-l-20 p-t-10 p-b-50 col-md-12 h-size2">
          <div className=" p-b-20">
            <div className="row no-gutters bg3 shadow1">
              <div className="col-md-3 ">
                <div className="w-size3 ">
                  <Skeleton height={100} width={100} />
                </div>
              </div>
              <div className="col-md-9">
                <div className="card-body">
                  <Link
                    to={`/motor`}
                    href="product-detail.html"
                    className="block2-name dis-block s-text4 p-b-5"
                  >
                    <Skeleton width={100} />
                  </Link>
                  <p className="s-text2">
                    <Skeleton width={100} />
                  </p>
                  <p className="block2-price s-text5 p-r-5">
                    <Skeleton width={100} />
                  </p>
                  <div>
                    <i className="fa fa-trash-alt"></i>
                  </div>
                  <a className="s-text1">
                    <Skeleton width={100} />
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );

    return (
      <React.Fragment>
        {this.state.loading ? (
          <SkeletonComponent />
        ) : (
          <section className=" p-t-10 col-md-12">
            <div className="bread-crumb flex-w p-l-20 p-t-10 p-l-15-sm">
              <Link to="/" className="s-text16">
                Home
                <i
                  className="fa fa-angle-right m-l-8 m-r-9"
                  aria-hidden="true"
                ></i>
              </Link>
              <span className="s-text17">Policies</span>
            </div>

            <div className="p-l-20 p-t-10 p-b-50 col-md-12 h-size2">
              {this.state.newPolicies.map((policy, index) => (
                <div key={index} className=" p-b-20">
                  <div className="row no-gutters bg3 shadow1">
                    <div className="col-md-3 ">
                      <div className="w-size3 ">
                        {(() => {
                          switch (policy.type) {
                            case "1":
                              return (
                                <img
                                  src={motor}
                                  className="cart-img-product b-rad-4 o-f-hidden p-t-10 p-l-10"
                                  alt="MOTOR"
                                />
                              );
                            case "2":
                              return (
                                <img
                                  src={funeral}
                                  className="cart-img-product b-rad-4 o-f-hidden p-t-10 p-l-10"
                                  alt="FUNERAL"
                                />
                              );
                            case "3":
                              return (
                                <img
                                  src={medical}
                                  className="cart-img-product b-rad-4 o-f-hidden p-t-10 p-l-10"
                                  alt="MEDICAL"
                                />
                              );
                            default:
                              return (
                                <img
                                  src={ITEM07}
                                  className="cart-img-product b-rad-4 o-f-hidden p-t-10 p-l-10"
                                  alt="OTHER"
                                />
                              );
                          }
                        })()}
                      </div>
                    </div>
                    <div className="col-md-9">
                      <div className="card-body">
                        <Link
                          to={`/`}
                          href="product-detail.html"
                          className="block2-name dis-block s-text4 p-b-5"
                        >
                          {policy.company}
                        </Link>

                        {(() => {
                          switch (policy.type) {
                            case "1":
                              return <p className="s-text2">motor</p>;
                            case "2":
                              return <p className="s-text2">Funeral</p>;
                            case "3":
                              return <p className="s-text2">medical</p>;
                            default:
                              return <p className="s-text2">other</p>;
                          }
                        })()}

                        {policy.status === 1 ? (
                          <p className="block2-price s-text5 p-r-5">active</p>
                        ) : (
                          <p className="block2-price s-text5 p-r-5">inactive</p>
                        )}

                        <div className="t-right p-b-30">
                          <Link
                            to={`/policies/${policy.policy_no}/carClaim`}
                            className="s-text4"
                          >
                            <i className="fa fa-plus p-r-10"></i>claim
                          </Link>
                        </div>

                        <div className="t-right">
                          <Link
                            to={`/policies/${policy.policy_no}/car`}
                            className="s-text4"
                          >
                            <i className="fa fa-plus p-r-10"></i>register car
                          </Link>
                        </div>
                        <a className="s-text1">MORE</a>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </section>
        )}
      </React.Fragment>
    );
  }
}

export default Claims;
