import React, { useState, useEffect } from "react";
import "./LandingPage.css";
import Logo from "../../assets/images/logos/totum.png";
import { AuthContext } from "../../store/UserContext";
import axios from "axios";

const LandingPage = () => {
  const [user, setUser] = useState({ cell: "", password: "" });

  const handleSubmit = (e) => {
    e.preventDefault();
    const logIn = [user];
    console.log(logIn);

    axios
      .post("http://142.93.207.225:4333" + "/getUserByCellAndPassword", logIn)
      .then(async (response) => {
        console.log(response.data);
        if (response.data?.length) {
          await localStorage.setItem("user_id", response.data[0].id);
          window.location.reload();
        } 
        else {
          console.log("the array is empty");
        }
      })
      .catch((error) => {
        console.log("::log in error::", error);
      });
  };

  return (
    <div class="">
      <div class="global-container">
        <div class="card login-form">
          <div class="card-body">
            <h3 class="card-title text-center">Log in to Totum</h3>
            <div class="card-text">
              <form>
                <div class="form-group">
                  <label for="exampleInputEmail1">Phone</label>
                  <input
                    value={user.cell}
                    type="phone"
                    class="form-control form-control-sm"
                    id="cell"
                    onChange={(e) => setUser({ ...user, cell: e.target.value })}
                  />
                </div>
                <div class="form-group">
                  <label for="exampleInputPassword1">Password</label>
                  <a href="#" style={{ float: "right" }}>
                    Forgot password?
                  </a>
                  <input
                    value={user.password}
                    type="password"
                    class="form-control form-control-sm"
                    id="password"
                    onChange={(e) =>
                      setUser({ ...user, password: e.target.value })
                    }
                  />
                </div>
                <button
                  class="btn btn-primary btn-block"
                  onClick={handleSubmit}
                >
                  Login
                </button>

                <div class="sign-up">
                  Don't have an account? <a href="/">Sign in</a>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LandingPage;
