import React, { Component } from "react";
import axios from "axios";

const apiUrl = "http://142.93.207.225:4333";

class Car extends Component {
  constructor(props) {
    super(props);
    this.state = {
      make: "",
      engine_no: "",
      registration_no: "",
      model_year: "",
      user_id: "",
      policy_id: "",
    };
  }

  componentWillMount() {
    this.setState({
      user_id: JSON.parse(localStorage.getItem("user_id")),
    });
    console.log(":: route parameters ::", this.props.match.params.id);
    this.setState({
      policy_id: this.props.match.params.id,
    });
  }

  handleMakeChange = (event) => {
    this.setState({
      make: event.target.value,
    });
  };
  handleEngineNumberChange = (event) => {
    this.setState({
      engine_no: event.target.value,
    });
  };
  handleRegistrationNumberChange = (event) => {
    this.setState({
      registration_no: event.target.value,
    });
  };
  handleModelYearChange = (event) => {
    this.setState({
      model_year: event.target.value,
    });
  };
  handleuser_idChange = (event) => {
    this.setState({
      user_id: event.target.value,
    });
  };
  handlepolicy_idChange = (event) => {
    this.setState({
      policy_id: event.target.value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const car = [
      {
        make: this.state.make,
        engine_no: this.state.engine_no,
        registration_no: this.state.registration_no,
        model_year: this.state.model_year,
        user_id: this.state.user_id,
        policy_id: this.state.policy_id,
      },
    ];
    console.log("Registration.js", car);
    axios
      .post(apiUrl + "/registerNewCar", car)
      .then((response) => {
        console.log(":: Car.js ::", response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  render() {
    return (
      <div className="p-l-20 p-t-50 col-md-12 h-size2 ">
        <h4 className="m-text3 p-t-30"> Register vehicle</h4>
        <form onSubmit={this.handleSubmit}>
          <div className="row p-t-10">
            <div className="col-md-6">
              <h6 className="s-text1">Make</h6>
              <div className="form-group w-size16 bo-rad-5">
                <input
                  type="text"
                  id="make"
                  name="make"
                  onChange={this.handleMakeChange}
                  className="form-control bo1   p-l-30 s-text2  size6"
                />
              </div>
            </div>
            <div className="col-md-6">
              <h6 className="s-text1">Engine number</h6>
              <div className="form-group w-size16 bo-rad-5">
                <input
                  type="text"
                  id="engine_no"
                  name="engine_no"
                  onChange={this.handleEngineNumberChange}
                  className="form-control bo1 s-text2  size6 p-l-30 "
                />
              </div>
            </div>
          </div>
          <div className="row p-t-10">
            <div className="col-md-6">
              <h6 className="s-text1">Registration number</h6>
              <div className="form-group w-size16 bo-rad-5">
                <input
                  type="text"
                  id="registration_no"
                  name="registration_no"
                  onChange={this.handleRegistrationNumberChange}
                  className="form-control bo1 s-text2  size6 p-l-30"
                />
              </div>
            </div>
            <div className="col-md-6">
              <h6 className="s-text1">year</h6>
              <div className="form-group w-size16 bo-rad-5">
                <input
                  type="text"
                  id="model_year"
                  name="model_year"
                  onChange={this.handleModelYearChange}
                  className="form-control bo-rad-5 bo1 s-text2  size6 p-l-30"
                />
              </div>
            </div>
          </div>

          <div className=" p-t-10">
            <button
              type="submit"
              className="flex-c-m size2 bg1 hov1  trans-0-4 bo-rad-5 bo1 w-size4 s-text1 h-size2"
            >
              Register
            </button>
          </div>
        </form>
      </div>
    );
  }
}

export default Car;
