import React, { useEffect, useState, match } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import swal from "sweetalert";
import "./Funeral.css";

import Car from "../../../assets/images/motor1.jpg";
import FuneralImg from "../../../assets/images/funeral.jpg";
import Banner from "../../../assets/images/banner.jpg";

const apiUrl = "http://142.93.207.225:4333";

const Funeral = ({ match }) => {
  const [item, setItem] = useState({});
  const [medExt, setMedExt] = useState([]);
  const [company, setCompany] = useState({});

  const history = useHistory();

  useEffect(() => {
    // fetchItem();
    console.log(match);
    // const user = JSON.parse(localStorage.getItem(user))
  }, []);

  const fetchItem = async () => {
    try {
      const fetchItem = await axios.get(
        `http://142.93.207.225:4335/getFuneralQuotationByQuotationId/${match.params.id}`
      );
      console.log("Motor.js response", fetchItem.data[0]);
      setItem(fetchItem.data[0]);

      const fetchMedExt = await axios.get(
        `http://142.93.207.225:4335/getMedaidExtentionQuotationByQuotationId/${match.params.id}`
      );
      console.log("Medical.js EXTENSION ", fetchMedExt.data[0]);
      setMedExt(fetchMedExt.data[0]);

      const fetchCompany = await axios.get(
        `http://142.93.207.225:4333/getCompanyByCompanyId/${fetchItem.data[0].company_id}`
      );
      console.log("getCompany response", fetchCompany.data[0]);
      setCompany(fetchCompany.data[0]);

    } catch (ex) {
      console.error(`:: carQuotes  :: ${ex.message}`);
    }
  };

  const handlePurchase = () => {
    swal({
      title: "Motor insurance",
      text: "payment successful!",
      icon: "success",
      button: "Ok",
    });
    history.push("/");
    // console.log(":: policy info ::", policy);
    // axios
    //   .post(apiUrl + "/registerUserPolicy", policy)
    //   .then((response) => {
    //     console.log(response.data);
    //   })
    //   .catch((error) => {
    //     console.log(error);
    //   });
  };

  return (
    <div className="">
      <div className="bread-crumb flex-w p-l-20 p-t-10 p-b-20 p-l-15-sm">
        <Link to="/" className="s-text16">
          Home
          <i className="fa fa-angle-right m-l-8 m-r-9" aria-hidden="true"></i>
        </Link>
        <Link to="/funeral" className="s-text16">
          Funeral
          <i className="fa fa-angle-right m-l-8 m-r-9" aria-hidden="true"></i>
        </Link>
        <span className="s-text17">{company.name}</span>
      </div>
      <div
        class="banner pos-relative mx-auto bo-rad-5 p-t-20"
        // styles={{ backgroundImage: `url(${Car})` }}
      >
        <div className="pos3  p-l-20" style={{ marginBottom: -30 }}>
          <img
            class=" img-thumbnail shadow1"
            src={FuneralImg}
            width="150"
            height="150"
            style={{}}
          />
        </div>
      </div>

      <div className=" p-t-40 p-r-20">
        <div className="row ">
          <div className="col-md-5 m-l-20">
            <div className="p-t-30 respon5">
              <h4 className="product-detail-name s-text2 p-b-13">
                Category: <span> </span>
                {item.category}
              </h4>

              <h4 className="product-detail-name s-text2 p-b-13">
                Subcategory: <span />
                {item.subcategory}
              </h4>
              <h4 className="product-detail-name s-text2 p-b-13">
                Period: <span />
                {item.period}
              </h4>
              <h4 className="product-detail-name s-text2 p-b-13">
                Processing time: <span />
                {item.processing_time} <span /> weeks
              </h4>
              <h4 className="product-detail-name s-text2 p-b-13">
                Amount: <span />${item.price}
              </h4>

              <div className="p-t-33 p-b-60 ">
                <div className="flex-r-m flex-w p-t-10">
                  <div className="w-size6 flex-m flex-w">
                    <div className="purchase-product size1 trans-0-4 m-t-10 m-b-10">
                      <button
                        className="flex-c-m size2 s-text2 bo3 hov1 trans-0-4"
                        type="button"
                        onClick={handlePurchase}
                      >
                        Purchase
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="col-md-3"></div>
          <p className="s-text2"> related products</p>
        </div>
      </div>
    </div>
  );
};
export default Funeral;
