import React, { Component } from "react";
import img00 from "../../../assets/images/motor.jpg";
import ITEM07 from "../../../assets/images/item-07.jpg";
import axios from "axios";
import { Link } from "react-router-dom";
// import $ from "jquery";

class Motors extends Component {
  constructor(props) {
    super(props);

    this.state = {
      cars: [],
    };

    this.carQuotes = this.carQuotes.bind(this);
  }

  componentDidMount() {
    (async () => {
      await this.carQuotes();
    })();
  }

  carQuotes = async () => {
    try {
      let url = `http://142.93.207.225:4335/getAllCarQuotes`;
      let { data } = await axios.get(url);
      console.log("response[0]", data[0]);
      this.setState({ cars: data[0] });
      console.log("car qoutations ::",this.state.cars)
    } catch (ex) {
      console.error(`:: carQuotes  :: ${ex.message}`);
    }
  };

  render() {
    // console.log(this.state.cars);

    return (
      <React.Fragment>
        <section className=" p-t-10 col-md-12">
          <div className="bread-crumb flex-w p-l-20 p-t-10 p-l-15-sm">
            <Link to="/" className="s-text16">
              Home
              <i
                className="fa fa-angle-right m-l-8 m-r-9"
                aria-hidden="true"
              ></i>
            </Link>
            <span className="s-text17">Motor</span>
          </div>

          <div className="p-l-20 p-t-10 p-b-50 col-md-12 h-size2">
            {this.state.cars.map((car, i) => (
              <div className=" p-b-20" key={car[0][0].id}>
                <div className="row no-gutters bg3 shadow1">
                  <div className="col-md-3 ">
                    <div className="w-size3 ">
                      <img
                        src={img00}
                        className="card-img-top h-100"
                        alt="..."
                      />
                    </div>
                  </div>
                  <div className="col-md-9">
                    <div className="card-body">
                      <Link
                        to={`/motor/${car[0][0].id}`}
                        className="block2-name dis-block s-text4 p-b-5"
                      >
                        {car[1][0].name}
                      </Link>
                      <p className="s-text2">
                        Car insurance or motor insurance protects you against
                        the costs you might incur if your vehicle gets damaged
                        or stolen. The cover is for personal motor vehicles
                        including cars, light delivery vehicles, trailers,
                        caravans and motorcycles. The insurance premium is based
                        on the market value of the vehicle.
                      </p>
                      <p className="block2-price s-text4 p-r-5">
                        ${car[0][0].price}
                      </p>
                      <a className="s-text1">MORE</a>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </section>
      </React.Fragment>
    );
  }
}
export default Motors;
