import React, { useEffect, useState } from "react";
import "./Vehicles.css";
import { Modal } from "react-bootstrap";
import axios from "axios";
import Home from "@material-ui/icons/Home";
import { Link, useHistory } from "react-router-dom";
import swal from "sweetalert";

const Vehicles = (props) => {
  const [cars, setCars] = useState([]);
  const [claim, setClaim] = useState({
    policy_no: "",
    location_of_loss: "",
    description: "",
    user_id: props.userId,
    policy_id: props.policyId,
    car_id: "",
    date_of_loss: "",
    third_party_receipient: "",
  });

  const [showEdit, setShowEdit] = useState(false);
  const [showClaim, setShowClaim] = useState(false);

  const handleEditClose = () => setShowEdit(false);
  const handleEditShow = () => setShowEdit(true);

  const handleClaimClose = () => setShowClaim(false);
  const handleClaimShow = () => {
    setShowClaim(true);
  };

  const handleClaim = (value) => {
    handleClaimShow();
    setClaim({ ...claim, car_id: value });
  };
  const handleClaimSubmit = () => {
    let details = [claim];
    console.log("::claim details", details);
    setShowClaim(false);
    axios
      .post("http://142.93.207.225:4334" + "/makeNewCarClaim", details)
      .then(async (response) => {
        console.log(response.data);
        swal({
          title: "Success",
          text: "claim submitted!",
          icon: "success",
          button: "Ok",
        });
      })
      .catch((error) => {
        console.log("::log in error::", error);
      });
  };

  useEffect(() => {
    getUserCars();
    console.log(":: Vehicle props::", props);
  }, []);

  const getUserCars = async () => {
    try {
      let url = `http://142.93.207.225:4333/getCarsForUser/${props.userId}`;
      let { data } = await axios.get(url);
      console.log(":: User Vehicles ::", data);
      setCars(data);
    } catch (ex) {
      console.error(`:: Vehicles error  :: ${ex.message}`);
    }
  };

  return (
    <div className="w-size6">
      <table className="table table-borderless">
        <thead>
          <tr className="s-text2">
            <th>Make</th>
            <th>Eng No</th>
            <th>Reg No</th>
            <th>Model yr</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody className="s-text1">
          {cars.map((car, i) => (
            <tr key={car.id} data-id={car.id}>
              <td>{car.make}</td>
              <td>{car.engine_no}</td>
              <td>{car.registration_no}</td>
              <td>{car.model_year}</td>
              <td>
                <Link className="p-l-10" title="Edit" onClick={handleEditShow}>
                  Edit
                </Link>
                <Link
                  className="p-l-10"
                  title="Claim"
                  onClick={() => handleClaim(car.id)}
                >
                  Claim
                </Link>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <Modal show={showEdit} onHide={handleEditClose} centered>
        <Modal.Header closeButton>
          <p className="s-text2">Edit details</p>
        </Modal.Header>
        <Modal.Body>
          <p className="s-text2 " style={{ marginBottom: 5 }}>
            Name
          </p>
          <div class="size15 bo4">
            <input
              class="sizefull s-text1 p-l-20"
              type="text"
              name="make"
              placeholder=" Make"
            />
          </div>
          <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
            Registration No
          </p>
          <div class="size15 bo4">
            <input
              class="sizefull s-text1 p-l-20"
              type="text"
              name="registration_no"
              placeholder="Registration number"
            />
          </div>
          <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
            Model Year
          </p>
          <div class="size15 bo4">
            <input
              class="sizefull s-text1 p-l-20"
              type="text"
              name="model_year"
              placeholder="model year"
            />
          </div>
          <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
            Engine No
          </p>
          <div class="size15 bo4">
            <input
              class="sizefull s-text1 p-l-20"
              type="text"
              name="engine_no"
              placeholder="engine number"
            />
          </div>
        </Modal.Body>
        <Modal.Footer>
          <div className="d-flex">
            <button
              className="flex-c-m size3 s-text2 bo3 hov1 bo-rad-5 trans-0-4"
              type="button"
              onClick={handleEditClose}
            >
              Close
            </button>
            <button
              className="flex-c-m m-l-20 size3 s-text2 bo3 hov1 bo-rad-5 bg1 trans-0-4"
              type="button"
              onClick={handleEditClose}
            >
              Save Changes
            </button>
          </div>
        </Modal.Footer>
      </Modal>

      <Modal show={showClaim} onHide={handleClaimClose} centered>
        <Modal.Header closeButton>
          <p className="s-text2">Make Claim</p>
        </Modal.Header>
        <Modal.Body>
          <form>
            <p className="s-text2 " style={{ marginBottom: 5 }}>
              Third part receipient
            </p>
            <div class="size15 bo4">
              <input
                value={claim.third_party_receipient}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="third_party_receipient"
                placeholder="Enter name of receipient "
                onChange={(e) =>
                  setClaim({ ...claim, third_party_receipient: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Location of loss
            </p>
            <div class="size15 bo4">
              <input
                value={claim.location_of_loss}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="location_of_loss"
                placeholder="Location of loss"
                onChange={(e) =>
                  setClaim({ ...claim, location_of_loss: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Accident description
            </p>
            <div class="size15 bo4">
              <input
                value={claim.description}
                class="sizefull s-text1 p-l-20"
                type="text"
                id="description"
                placeholder="Location of loss"
                onChange={(e) =>
                  setClaim({ ...claim, description: e.target.value })
                }
              />
            </div>
            <p className="s-text2 p-t-20" style={{ marginBottom: 5 }}>
              Date of Loss
            </p>
            <div class="size15 bo4">
              <input
                value={claim.date_of_loss}
                class="sizefull s-text1 p-l-20"
                type="date"
                id="date_of_loss"
                placeholder="Date of loss"
                onChange={(e) =>
                  setClaim({ ...claim, date_of_loss: e.target.value })
                }
              />
            </div>
          </form>
        </Modal.Body>
        <Modal.Footer>
          <div className="d-flex">
            <button
              className="flex-c-m size3 s-text2 bo3 hov1 bo-rad-5 trans-0-4"
              type="button"
              onClick={handleClaimClose}
            >
              Close
            </button>
            <button
              className="flex-c-m m-l-20 size3 s-text2 bo3 hov1 bo-rad-5 bg1 trans-0-4"
              type="button"
              onClick={handleClaimSubmit}
            >
              Send Claim
            </button>
          </div>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default Vehicles;
