import React, { Component } from "react";
import axios from "axios";

const apiUrl = "http://142.93.207.225:4334";

class CarClaim extends Component {
  constructor(props) {
    super(props);
    this.state = {
      policy_no: "1",
      location_of_loss: "",
      description: "",
      user_id: "",
      policy_id: "",
      car_id: "1",
      date_of_loss: "",
      third_party_receipient: "",
      cars: [],
    };
    this.cars = this.cars.bind(this);
  }

  componentWillMount() {
    (async () => {
      await this.cars();
    })();

    this.setState({
      user_id: JSON.parse(localStorage.getItem("user_id")),
    });
    console.log(":: route parameters ::", this.props.match.params.id);
    this.setState({
      policy_id: this.props.match.params.id,
    });
  }

  cars = async () => {
    try {
      let url = `http://142.93.207.225:4333/getCarsForUser/${localStorage.getItem('user_id')}`;
      let { data } = await axios.get(url);
      console.log("response[0]", data);
      this.setState({ cars: data });
    } catch (ex) {
      console.error(`:: cars  :: ${ex.message}`);
    }
  };

  handleLocationChange = (event) => {
    this.setState({
      location_of_loss: event.target.value,
    });
  };
  handleDescriptionChange = (event) => {
    this.setState({
      description: event.target.value,
    });
  };
  handleDateOfLossChange = (event) => {
    this.setState({
      date_of_loss: event.target.value,
    });
  };
  handleReceipientChange = (event) => {
    this.setState({
      third_party_receipient: event.target.value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const claim = [
      {
        policy_no: this.state.policy_no,
        location_of_loss: this.state.location_of_loss,
        description: this.state.description,
        user_id: this.state.user_id,
        policy_id: this.state.policy_id,
        car_id: this.state.car_id,
        date_of_loss: this.state.date_of_loss,
        third_party_receipient: this.state.third_party_receipient,
      },
    ];

    console.log("CarClaim.js", claim);
    axios
      .post(apiUrl + "/makeNewCarClaim", claim)
      .then((response) => {
        console.log(":: CarClaim.js ::", response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  render() {
    return (
      <div className="p-l-20 p-t-50 col-md-12 h-size2 ">
        <h4 className="m-text3 p-t-30">Claim form</h4>

        <p className="s-text2">select vehicle</p>
        <div class="rs2-select2 bo4 of-hidden w-size12 m-t-5 m-b-5 m-r-10">
          <select class="selection-2 s-text2">
            {this.state.cars.map((car, i) => (
          <option key={car.id}>{car.make}</option>
            ))}
          </select>
          
        </div>
        <form onSubmit={this.handleSubmit}>
          <div className="row p-t-10">
            <div className="col-md-6">
              <h6 className="s-text1">Location of Loss</h6>
              <div className="form-group w-size16 bo-rad-5">
                <input
                  type="text"
                  id="location_of_loss"
                  name="location_of_loss"
                  onChange={this.handleLocationChange}
                  className="form-control bo1   p-l-30 s-text2  size6"
                />
              </div>
            </div>
            <div className="col-md-6">
              <h6 className="s-text1">description</h6>
              <div className="form-group w-size16 bo-rad-5">
                <input
                  type="text"
                  id="description"
                  name="description"
                  onChange={this.handleDescriptionChange}
                  className="form-control bo1 s-text2  size6 p-l-30 "
                />
              </div>
            </div>
          </div>
          <div className="row p-t-10">
            <div className="col-md-6">
              <h6 className="s-text1">Date of loss</h6>
              <div className="form-group w-size16 bo-rad-5">
                <input
                  type="text"
                  id="date_of_loss"
                  name="date_of_loss"
                  onChange={this.handleDateOfLossChange}
                  className="form-control bo1 s-text2  size6 p-l-30"
                />
              </div>
            </div>
            <div className="col-md-6">
              <h6 className="s-text1">Thirdy party receipient</h6>
              <div className="form-group w-size16 bo-rad-5">
                <input
                  type="text"
                  id="third_party_receipient"
                  name="third_party_receipient"
                  onChange={this.handleReceipientChange}
                  className="form-control bo-rad-5 bo1 s-text2  size6 p-l-30"
                />
              </div>
            </div>
          </div>

          <div className=" p-t-10">
            <button
              type="submit"
              className="flex-c-m size2 bg1 hov1  trans-0-4 bo-rad-5 bo1 w-size4 s-text1 h-size2"
            >
              Send claim
            </button>
          </div>
        </form>
      </div>
    );
  }
}

export default CarClaim;
